.program-benefits {
  background: #111518;
  padding-top: 50px;

  margin: 0 auto;

  @media screen and (min-width: 768px) {
    padding-top: 60px;
  }

  @media screen and (min-width: 1366px) {
    overflow: hidden;
    padding-top: 65px;
    padding-bottom: 65px;
    width: auto;
  }
}
.program-benefits-title {
  margin-bottom: 30px;
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  text-align: center;
  letter-spacing: 0.03em;
  color: #ffffff;

  @media screen and (min-width: 768px) {
    text-align: left;
  }
  @media screen and (min-width: 1366px) {
    font-size: 46px;
    line-height: 1.3;
  }
}
.program__box h3 {
  margin-bottom: 20px;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 0.5;

  letter-spacing: 0.08em;
  text-transform: uppercase;

  color: #ffffff;
  @media screen and (min-width: 1366px) {
    font-size: 20px;
    line-height: 1.4;
    & span {
      font-style: normal;
      font-weight: 700;
      letter-spacing: 0.08em;
      text-transform: uppercase;

      font-size: 30px;
      line-height: 1.4;
    }
  }
}
.program__img {
  width: 12px;
  height: 12px;
  position: absolute;
  border-color: transparent;
  border-radius: 50%;
  background-color: #ff6b09;
  top: -2px;
  left: -6px;

  @media screen and (min-width: 1366px) {
    top: -6px;
    left: -2px;
    width: 16px;
    height: 16px;
  }
}
.program {
  // position: relative;
  padding-left: 64px;
  padding-bottom: 70px;

  &__box {
    position: relative;
    padding-left: 20px;
    margin: 0 auto;
  }
  &__box:not(:last-child) {
    border-left: 1px solid white;
    padding-bottom: 40px;
  }
  &__box-img::after {
    content: '';
    position: absolute;
    top: 85px;
    right: calc(50% - 128px);
    width: 120px;
    // width: 250px;
    height: 137px;
    display: block;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(../images/program-benefits/program-mob.png);
    // background-image: url(../images/program-benefits/program-bg.jpg);
  }
  &__list {
    padding-bottom: 8px;
  }

  &__title {
    position: relative;
    margin-bottom: 15px;
    padding-left: 20px;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.6;

    letter-spacing: 0.08em;

    color: #ff6b09;
  }

  &__title::before {
    content: '';
    position: absolute;
    top: 9px;
    left: 0;
    width: 12px;
    height: 2px;
    background-color: #ff6b09;
    display: block;
  }
  &__title-final {
    color: transparent;
    font-size: 20px;
    line-height: 1.6;
  }

  &__list p {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.5;

    letter-spacing: 0.08em;

    color: #b9b9b9;

    opacity: 0.5;
  }
  @media screen and (min-width: 768px) {
    padding-left: 35px;
    &__conteiner {
      display: flex;
    }

    &__box {
      position: relative;
      padding-left: 20px;
      margin: 0 auto;
    }
    &__box-img::after {
      content: '';
      position: absolute;
      top: -99px;
      right: 0;
      width: 374px;

      height: 273px;
      display: block;
      background-size: contain;
      background-repeat: no-repeat;
      background-image: url(../images/program-benefits/program-bg.jpg);
    }
    &__box:not(:last-child) {
      border-left: 1px solid white;
      padding-bottom: 40px;
    }
    &__list:not(:last-child) {
      margin-right: 50px;
    }
  }
  @media screen and (min-width: 1366px) {
    width: auto;
    position: relative;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    padding: 0;

    &__conteiner {
      display: block;
    }
    &__title {
      font-size: 20px;
      line-height: 1.6;
      padding-left: 30px;
    }
    &__title::before {
      width: 20px;
      top: 15px;
      left: 0;
    }
    &__box-img::after {
      display: none;
    }

    &__box {
      position: relative;
      padding-left: 0;
      margin: 0;
      width: 226px;
      border-top: none;
      padding-bottom: 0;
      padding-top: 40px;
    }
    &__box:not(:last-child) {
      margin-right: 50px;
    }
    &__box::before {
      content: '';
      position: absolute;
      top: 0;
      left: calc(-52vw - -650px);
      width: calc(100vw - 10px);
      border-top: 1px solid white;
    }

    &__list {
      margin-right: 0;
    }

    &__list:not(:last-child) {
      margin-right: 0;
    }
    &__box:not(:last-child) {
      border-left: none;
    }
  }
}
