.contacts {
  padding: 55px 0;
  background-color: #111518;
  background-image: url(../images/bg/mob-bg1.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  @media screen and (min-device-pixel-ratio: 2),
    screen and (-moz-min-device-pixel-ratio: 2),
    screen and (-o-min-device-pixel-ratio: 2 / 1),
    screen and (-webkit-min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/bg/mob-bg2.png);
  }
  @media (min-width: 768px) {
    background-image: url(../images/bg/tab-bg1.png);
  }
  @media screen and (min-device-pixel-ratio: 2),
    screen and (-moz-min-device-pixel-ratio: 2),
    screen and (-o-min-device-pixel-ratio: 2 / 1),
    screen and (-webkit-min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/bg/tab-bg2.png);
  }
  @media (min-width: 1200px) {
    background-image: url(../images/bg/desk-bg1.png);
  }
  @media screen and (min-device-pixel-ratio: 2),
    screen and (-moz-min-device-pixel-ratio: 2),
    screen and (-o-min-device-pixel-ratio: 2 / 1),
    screen and (-webkit-min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/bg/desk-bg2.png);
  }
}
.contacts__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contacts__title {
  margin: 0 auto;
  max-width: 800px;

  font-weight: 800;
  font-size: 14px;
  line-height: 1.3;

  text-align: center;
  letter-spacing: 0.03em;

  color: #ffffff;
  @media screen and(min-width:768px) {
    font-size: 26px;
  }
  @media screen and(min-width:1366px) {
    font-size: 46px;
  }
}
.accent {
  color: #ff6b09;
}
.contacts__text {
  margin: 0 auto;
  margin-bottom: 43px;

  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  text-align: center;
  letter-spacing: 0.08em;
  color: #ffffff;
  @media screen and(min-width:768px) {
    font-size: 16px;
  }
  @media screen and(min-width:1366px) {
    font-size: 30px;
    line-height: 1.23;
    max-width: 1080px;
  }
}
.contacts__input {
  height: 48px;
  max-width: 280px;
  width: 100%;
  padding: 28px 15px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;

  letter-spacing: 0.08em;

  color: #ffffff;
  background-color: transparent;
  border-color: #fff;
  margin-bottom: 20px;
  transform: scale(1);

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-radius 250ms cubic-bezier(0.4, 0, 0.2, 1), box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1), background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and(min-width:768px) {
    max-width: 390px;
  }
  @media screen and(min-width:1366px) {
    max-width: 478px;
    height: 78px;
  }
  &:hover,
  &:active,
  &:focus {
    border-color: #ff6b09;
    border-radius: 20px;
    transform: scale(1.1);
  }
}
.contacts__input:invalid {
  color: red;
  border: 2px dashed red;
}
.contacts__input:valid {
  color: green;
}
.form__input::placeholder {
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;

  letter-spacing: 0.08em;

  color: #ffffff;
}
.contacts__button {
  display: block;
  max-width: 260px;
  width: 100%;
  height: 48px;
  border: none;
  color: #fff;
  background: #ff6b09;
  text-transform: uppercase;

  font-weight: 700;
  font-size: 12px;
  line-height: 1.25;
  cursor: pointer;
  transform: scale(1);
  border: 2px solid #ff6b09;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-radius 250ms cubic-bezier(0.4, 0, 0.2, 1), box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1), background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  letter-spacing: 0.08em;
  @media screen and(min-width:1366px) {
    max-width: 354px;
    height: 70px;
  }
  &:hover,
  &:active,
  &:focus {
    border-radius: 20px;
    color: #000000;
    box-shadow: rgba(0, 0, 0, 0.04);
    transform: scale(1.1);
  }
}
